header{
    height: 100vh;
    padding-top:  2rem; 
    overflow: hidden;

}
.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/* =====CTA===== */
.cta{
    margin-top: 1rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* HEADER SOCIALS */
.header__social{
     display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__social::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}
 
/* me  */
.me{
background: linear-gradient(var(--color-primary),transparent);
    width: 22rem;
    height: 40rem;
    position: absolute;
    left: calc(60% - 16rem);
    border-radius: 5rem 6rem 6rem 5rem;
    margin-top: 2rem;
    overflow: hidden;
    /* padding: 5rem  1.5rem 1.5rem 1.5rem; */

}
.scroll__down{
position: absolute;
right: -2.3rem;
bottom: 5rem;
transform: rotate(90deg);
font-weight: 200;
font-size: 0.9rem;
}


/* MEDIA QUERY FOR TABLETS */
@media  screen and (max-width:1024px) 
{
    header{
        height: 68vh;
    }
    
}
@media  screen and (max-width:600px) 
{
    header{
        height: 100vh;
    }
    .header__social,.scroll__down{
        display: none;

    }
    
}