@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@700&family=Poppins:ital,wght@0,400;0,500;0,600;1,300&display=swap');

*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}
:root{
    --color-bg:#1f1f38;
    --color-bg-variant:#2c2c6c;
    --color-primary:#4db5ff;
    --color-primary-variant:rgba(77,181,255,0.4);
    --color-white:#fff;
    --color-light:rgba(255,255,255,0.4);


    /* --color-bg:#fac7b7;
    --color-bg-variant:#ff7984;
    --color-primary:#53a0ac;
    --color-primary-variant:rgba(67, 182, 235, 0.4);
    --color-white:#fcf5f5;
    --color-light:rgba(8, 8, 8, 0.4); */

    /* ======black theme======= */

    /* --color-bg:#0a0a0a;
    --color-bg-variant:#d88fa2;
    --color-primary:#76bdcf;
    --color-primary-variant:rgba(235, 235, 235, 0.4);
    --color-white:#2f0bbe;
    --color-light:rgba(255,255,255,0.4); */

    --container-width-lg:75%;
    --container-width-md:86%;
    --container-width-sm:90%;

    --transition: all 400ms ease;

}
::-webkit-scrollbar{
    display: none;
}
body{
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
}

/* GENERAL STYLES */

.container{
    width: var(--container-width-lg);
    margin: 0 auto;
}
h1,h2,h3,h4,h5,h6{
    font-weight: 500;
}

h1{
    font-size: 2.5rem;
}

section{
     margin-top: 8rem ;
     /* height: 100vh; */
}
section>h2, section>h5{
    text-align: center;
    color: var(--color-light);
}
section>h2{
    color: var(--color-primary);
    margin-bottom: 3rem;
}
.text-light{
    color: var(--color-light);
}
a{
    color: var(--color-primary);
    transition: var(--transition);
}
a:hover{
    color: var(--color-white);
}

.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: .4rem;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
    cursor: pointer;
}
.btn:hover{
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}
.btn-primary{
    background: var(--color-primary);
    color: var(--color-bg);
}
img{
    display: inline-block;
    width: 100%;
    object-fit: cover;
}

/* =========MEDIA QUERY FOR TABLETS============ */
@media screen and (max-width:1024px) {
    .container{
        width: var(--container-width-md);
    }
    section{
        margin-top: 6rem;

    }
   
    
}

/* =========MEDIA QUERY FOR Mobiles============ */
@media screen and (max-width:600px) {
    .container{
        width: var(--container-width-sm);
    }
    section>h2{
        margin-bottom: 2rem;
        
    }
    
}